import counter from '../../../components/counter.vue'
	export default{
		components:{
			counter
		},
		data(){
			return{
				footerbottom:0,
				theIndex:null,//控制滑动效果 当前滑动下标
				oldIndex:null,//上一个左滑下标
				goodsList:[],
				selectedList:[],
				isAllSelected:false,
				sumPrice:'0.00'
			}
		},
		methods:{
			add(item){
				item.number++
				this.sum()
			},
			sub(item){
				if(item.number <= 1){
					return
				}
				item.number--
				this.sum()
			},
			handleCheckbox(item){ //单选
				item.selected = !item.selected
				//条件:数组中是否包含该元素
				//把选中当前的商品数据给isExist
				let isExist = this.selectedList.indexOf(item)
				if(isExist > -1){
					this.selectedList.splice(isExist,1)
				}else{
					//push数组里
					this.selectedList.push(item)
				}
				
				//全选状态
				if(this.selectedList.length == this.goodsList.length){
					this.isAllSelected = true
				}else{
					this.isAllSelected = false
				}
				
				//调用合计sum
				this.sum()
				
			},
			handleSelectedAll(){//全选
			   //取反 开始为false 
				this.isAllSelected = !this.isAllSelected
				
				//数据处理
				let arr = []
				this.goodsList.forEach((item,i)=>{
					//this.isAllSelected取反给item 变一个新数组
					// 选中为true
					item.selected = this.isAllSelected
					//改变item.selected状态 为true 插入arr
					arr.push(item)
				})
				//如果==为true 就为arr
				this.selectedList = this.isAllSelected ? arr:[]				
				//调用合计sum
				this.sum()
			},
			sum(){//合计
			      this.sumPrice = 0;
			      this.goodsList.forEach((item,i) => {
			      	if(item.selected){
						//合计=合计+数量*价格
			      		this.sumPrice = this.sumPrice + (item.number * item.price)
			      	}
			      })
				  //精准小数点两位
				  this.sumPrice  = this.sumPrice.toFixed(2)	
			},
			handleSingleDelete(item){ //滑动删除
				//单选删除
				// 更新storage
				uni.getStorage({
					key:"goodsList",
					success: (res) => {
						res.data.splice(res.data.indexOf(item),1);
						uni.setStorageSync("goodsList",res.data);
					}
				})
				
				// 更新数组
				this.goodsList.splice(this.goodsList.indexOf(item),1);
				this.selectedList.splice(this.selectedList.indexOf(item),1);
				//滑动坐标为空
				this.oldIndex = null;
				this.theIndex = null;
				//调用sun 刷新价格
				this.sum();
			},
			handleMulDelete(){ //左下角删除按钮
				//循环删除所有选中商品
				//直到this.selectedList.length = 0 跳出
				console.log(this.selectedList.length)
				while(this.selectedList.length > 0){
					//调用handleSingleDelete函数 传入参数 里面有就删除
					this.handleSingleDelete(this.selectedList[0])
				}
				//初始化数据
				this.selectedList = []
				//选中为false
				this.isAllSelected = false
				//调用sum
				this.sum()
			},
			handleGoodsInfo(item){
				uni.navigateTo({
					url:"../../goods/goods?goodsInfo="+JSON.stringify(item)
				})
			},
			handleTouchStart(index,$event){
				//console.log(event)
				//多点触控 不触发
				if(event.touches.length>1){
					return
				}
				//初始化坐标
				this.initXY = [event.touches[0].pageX,event.touches[0].pageY]
			},
			handleTouchMove(index,$event){
				//多点触控 不触发
				if(event.touches.length>1){
					return
				}
				
				//移动位置
				let moveX = event.touches[0].pageX - this.initXY[0]
				let moveY = event.touches[0].pageX - this.initXY[1]
				
				 //滑动位置小 不触发
				 if(Math.abs(moveX) < 5){
					 return
				 }
				 //竖向滑动 不触发
				 if(Math.abs(moveY) > Math.abs(moveX)){
					 return
				 }
				//左滑
				if(moveX < 0){
					this.theIndex = index
				}else{
					this.oldIndex = this.theIndex
					this.theIndex = null
				}
			},
			handleTouchEnd(index,event){//滑动结束
				
			},
			handleConfirm(){//结算
				if(this.selectedList.length < 1){
					uni.showToast({
						title:"请选择结算的商品",
						icon:"none"
					})
					return
				}
				//本地存储
				uni.setStorage({
					key:"confirmList",
					data:this.selectedList,
					success:()=>{
						uni.navigateTo({
							url:"../../order/confirm"
						})
					}
				})
			}
		},
		onShow(){
			uni.getStorage({
				key:"goodsList",
				success:((res)=>{
					//console.log(res)
					//将所有商品的选中状态都设置false
					for(let i=0;i<res.data.length;i++){
						res.data[i].selected = false
					}
					this.goodsList = res.data
					
					//属性数据的初始化
					this.selectedList = []
					this.isAllSelected = false
					this.sumPrice = '0.00'
				})
			})
		},
		onLoad(){
			// 兼容h5下的底部菜单



		}
	}
